.app-wrapper {
    display: grid;
    margin: auto;
    width: 100%;
    height: 960px;
    grid-template-areas:
  "h c"
  "n c";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.app-wrapper-content {
        width: 70%;
        height: 960px;
        grid-area: c;
        background-color: cornflowerblue;
}

