    font-size: 20px;
    text-shadow: 2px 1px 2px black,
    1px 2px 1px black,
    2px 1px 2px black,
    1px 2px 1px black;
}

.error input {
    border: 2px solid red;
}

.error span {
    color: red;
    text-shadow: 2px 1px 2px black,
    1px 2px 1px black,
    2px 1px 2px black,
    1px 2px 1px black;
    font-size: 17px;
}